$black: #3A001E;
$white: #FFF6F0;

$dark: #C5471F;
$primary: #FF7F52;
$accent: #FF9C6C;
$light: #FFB987;

$offset: 0.5rem;

$backgroundImage: linear-gradient(to right top, #ff7f52ff, #ff8f5dff, #ff9e69ff, #ffac78ff, #ffb987ff);

//main stuff
.container {
    display: flex;
    width: 40vw;
    height: 100%;
    padding: 1rem;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    .name {
        padding: 2rem 3.5rem 2rem 3.5rem;
        display: block;
        width: fit-content;
        height: 20%;
        white-space: nowrap;

        font-weight: 900;
        font-size: 4.5rem;
        color: $primary;

        .background {
            display: block;
            padding: $offset 2rem $offset 2rem;
            position: absolute;
            top: 2rem;
            transform: translateX(-2rem) translateY(0.5rem);
            width: fit-content;
            transition: 0.3s all ease-in-out;
            opacity: 0;
        }
    }

    .name:hover {
        cursor: pointer;
        .background {
            background-image: $backgroundImage;
            opacity: 1;
            color: $white;
            transition: 0.3s all ease-in-out;
        }
    }
    .text {
        //content
        align-self: flex-start;
        h1 {
            color: $primary;
            font-weight: 900;
            font-size: 1.5rem;
            margin: 0.2rem;
            margin-bottom: 0.5rem;
        }

        em {
            font-style: normal;
            padding: 0.5rem;
        }

        p {
            margin: 0.2rem;
            margin-bottom: 0.5rem;
            font-size: 1.2rem;
            color: $black;

            strong {
                font-weight: 900;
            }
        }

        a:hover {
            text-decoration: underline;
        }
    }

    //table-cell
    .table {
        display: flex;
        flex-direction: column;
        align-self: flex-start;
        width: 100%;
        .cell {
            border: 0.15rem solid $primary;
            box-shadow: 0px 0px 3px 3px $primary;
            border-radius: 2rem;
            background-color: #FFF;
            padding: 1rem;
            width: 100%;
            height: 20vh;
            max-height: 20vh;
            display: flex;
            margin: 1rem;
            overflow: scroll;

            div {
                padding: 0.5rem;
                color: $black;

                p {
                    margin: 0.3rem;
                    font-size: 1.1rem;
                }

                p:nth-child(1) {
                    font-weight: 900;
                }

                p:nth-child(2) {
                    font-weight: 400;
                }

                p:nth-child(3) {
                    color: $accent;
                    font-size: 0.9rem;
                    padding-bottom: 1.5rem;
                }

                p:nth-child(3):hover {
                    text-decoration: underline;
                }
            }

            div:nth-child(1) {
                position: sticky;
                top: 0;
            }
        }
    }
}

//misc stuff
.everything {
    width: 100vw;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.four {
    width: fit-content;
    display: inline-block;
    align-content: center;
    justify-content: center;
    position: absolute;
    color: $dark;
    font-size: 5rem;
    font-weight: 200;

    span {
        font-weight: 900;
        margin-right: 3rem;
    }
}

.footer {
    color: $dark;
    padding: 1.2rem;
    width: 100vw;
    background-color: $white;
    display: flex;
    justify-content: space-around;
    align-items: center;

    * {
        text-align: center;
    }
}